@import "../../config.scss";

.drop-down-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: $secondary-clr;
  max-width: 180px;
  border-radius: 10px;
  padding: 5px 10px;
  & select {
    display: block;
    background-color: inherit;
    color: $primary-clr;
    border: none;
    outline: none;
    margin-top: 10px;
    cursor: pointer;
  }
}

@media (max-width: 450px) {
  .drop-down-list {
    font-size: 10px;
    padding: 5px 5px;
    & select {
      margin-top: 5px;
      font-size: 8px;
    }
  }
}
